package pages

import kotlinx.browser.document
import kotlinx.browser.window
import kotlinx.html.*
import org.w3c.dom.HTMLElement
import org.w3c.dom.url.URLSearchParams


interface DataViewPage : Page {


    val params : URLSearchParams get() {

        fun addQParam() {
            if(! window.location.href.contains("?")) {
                console.log("adding url param")
                window.location.href = window.location.href + "?"
            }
        }

            addQParam()

            return URLSearchParams(
                window.location.href.split("?")[1]
                    .split("#")[0]
        )
    }



    fun FlowContent.infobardiv(
        imgUrl :String,
        imgTxt :String,
        block: DIV.() -> Unit
    ) :Unit =
        div("infobar") {
            //user image and name
            div {
                style = "padding: 0; margin: 0; width: 8em; display: inline; float: left;"
                img(classes = "profileImage") {
                    style = "display: block; "
                    src = "${imgUrl}"
                }
                span {
                    style = "display: block; font-size: 1.35em; font-weight: bold;"
                    +"${imgTxt}"
                }
            }
            block();
        }





    fun TagConsumer<HTMLElement>.dataviewdiv(
        block: DIV.() -> Unit
    ) :Unit =
        DIV(attributesMapOf("class", "dataviewlist"), this).visit(block)



}