package pages

import Requester
import kotlinx.browser.document
import kotlinx.browser.window
import kotlinx.coroutines.GlobalScope
import kotlinx.coroutines.launch
import kotlinx.html.*
import kotlinx.html.dom.append
import kotlinx.html.js.onClickFunction
import kotlinx.html.js.onTouchEndFunction
import kotlinx.html.js.onTouchStartFunction
import misc.Swiper.htmlTabs


import misc.Swiper.initTabs
import models.Profile
import org.w3c.dom.HTMLElement
import org.w3c.dom.url.URLSearchParams
import pages.ProfilePage.dataviewdiv

object ProfilePage : DataViewPage {

    override fun loadPage() {

        document.body?.append {

            GlobalScope.launch {





                val qUid = params.get("uid")


                if (qUid == null)
                    window.location.href = window.location.origin + "spotify/profile/me"


                //awaited requests:
                val user = Requester().getUser(qUid!!)
                user.playlists = Requester().getUsersPlaylists(qUid!!);


                //dev log:
                console.info(user)
                console.info(user.playlists)


                //user info bar

                infoBar(user, qUid)

                htmlTabs()
                initTabs()


                /*
            //init swiper
            initSwiperHtml()
            initSwiper()
             */


                //playlist scrollable holder thing
                dataView(user)


            }

        }
    }


    inline fun TagConsumer<HTMLElement>.infoBar(user: Profile, qUid: String) = div {

            infobardiv(
                imgUrl = user.imageUrl,
                imgTxt = user.displayName,

            ) {



                    li {
                        +"Followers: ${user.followers}"
                    }



                //user id bottom right
                small {
                    style = "text-align: right; flex-grow: 1; float: right; margin-right: 2vw; "
                    +"$qUid"
                }

                br { style = "break: both;" }
            }
        }
    }



    inline fun TagConsumer<HTMLElement>.dataView(user: Profile) {


        div {
            dataviewdiv {



            user.playlists?.forEach {

                div(classes = "card") {

                    onTouchStartFunction = {
                        it.target.asDynamic().style.boxShadow = "inset 0.1em 0.1em 0.3em -0.03em rgba(20,20,20,0.6)"
                    }
                    onTouchEndFunction = {
                        it.target.asDynamic().style.boxShadow = "none";
                    }

                    onClickFunction = { ev ->
                        window.location.href = "${window.location.origin}/spotify/playlist/?pid=${it.id}"
                    }


                    //name
                    span("playlistnamespan") { +it.name };
                    // num tracks (right of name)
                    i {
                        style =
                            "float:right; text-align: right; width: 16%; font-size: 0.7em; white-space: nowrap; "; +"${it.numTracks} tracks"
                    }

                    br { style = "clear: both;" }


                    //description
                    span("playlistPropertyLabel") {
                        +"description:";
                        i("playlistPropertyContent") { +it.description }
                    }

                }
            }
            }
        }
    }
