    inline fun TagConsumer<Any>.initTabs(

    ) {
        js("""
            $('.jTabs').slick({
                ltr: true,
                arrows: false,
                dots: true,
                dotsClass: "slick-dots",
                
                customPaging : function(slider, i) {
                    return ( "<a><img /> </a>" ); 
                },
                                
            });
        """)

